.project_card {
    width: 20em;
    margin: 0.5%;
    padding: 1em;
    border: 1px solid #7a7a7a;
    border-radius: 5px;
    margin: 0 auto;
}

.project_card h4 {
    background-color: #222;
    color: #ffbb33;
    text-transform: uppercase;
    border-radius: 5px;
    padding: .4em;
    margin-bottom: 1.3em;
    font-size: 1.3em;
    text-align: center;
}

.project_card p {
    color: #7a7a7a;
    margin-bottom: .5em;
}

.project_card p span {
    font-weight: bold;
}

.category_text {
    display: flex;
    align-items: center;
}

.category_text span {
    display: inline-block;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: #ccc;
}

.category_text .infra {
    background-color: #ff4ebc;
}

.category_text .desenvolvimento {
    background-color: #a0e7e5;
}

.category_text .design {
    background-color: #84f8c8;
}

.category_text .planejamento {
    background-color: #fbe7c6;
}

.project_card_actions {
    margin-top: 1.2em;
    display: flex;
    align-items: center;
}

.project_card_actions a,
.project_card_actions button {
    background-color: #fff;
    color: #222;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    font-size: .9em;
    border: none;
    border: 1px solid #222;
    border-radius: 5px;
    padding: .4em 1em;
    margin-right: 1em;
    cursor: pointer;
    transition: 0.5s;
}

.project_card_actions svg {
    margin-right: .5em;
}

.project_card_actions a:hover, 
.project_card_actions button:hover {
    background-color: #222;
    color: #ffbb33;
}


